              { v: this.categoriaData.cat1.sesentaycuatro, f: this.categoriaData.cat1.sesentaycuatro },
              { v: this.categoriaData.cat1.cincuentaycuatro, f: this.categoriaData.cat1.cincuentaycuatro },
