              { v: this.categoriaData.cat3.sesentaycuatro, f: this.categoriaData.cat3.sesentaycuatro },
              { v: this.categoriaData.cat3.cincuentaycuatro, f: this.categoriaData.cat3.cincuentaycuatro },
