              { v: this.categoriaData.cat5.sesentaycinco, f: this.categoriaData.cat5.sesentaycinco },
              { v: this.categoriaData.cat5.sesentaycuatro, f: this.categoriaData.cat5.sesentaycuatro },
              { v: this.categoriaData.cat5.cincuentaycuatro, f: this.categoriaData.cat5.cincuentaycuatro },
              { v: this.categoriaData.cat5.cuarentaycuatro, f: this.categoriaData.cat5.cuarentaycuatro },
              { v: this.categoriaData.cat5.treintaycuatro, f: this.categoriaData.cat5.treintaycuatro },
              { v: this.categoriaData.cat5.veinticinco, f: this.categoriaData.cat5.veinticinco }
            ]
          },
          {
            c: [
              { v: 0, f: 'Cat 6 CCT 366/06' },
              { v: this.categoriaData.cat6.sesentaycinco, f: this.categoriaData.cat6.sesentaycinco },
              { v: this.categoriaData.cat6.sesentaycuatro, f: this.categoriaData.cat6.sesentaycuatro },
              { v: this.categoriaData.cat6.cincuentaycuatro, f: this.categoriaData.cat6.cincuentaycuatro },
              { v: this.categoriaData.cat6.cuarentaycuatro, f: this.categoriaData.cat6.cuarentaycuatro },
              { v: this.categoriaData.cat6.treintaycuatro, f: this.categoriaData.cat6.treintaycuatro },
              { v: this.categoriaData.cat6.veinticinco, f: this.categoriaData.cat6.veinticinco }
            ]
          },
          {
            c: [
              { v: 0, f: 'Cat 7 CCT 366/06' },
              { v: this.categoriaData.cat7.sesentaycinco, f: this.categoriaData.cat7.sesentaycinco },
              { v: this.categoriaData.cat7.sesentaycuatro, f: this.categoriaData.cat7.sesentaycuatro },
              { v: this.categoriaData.cat7.cincuentaycuatro, f: this.categoriaData.cat7.cincuentaycuatro },
              { v: this.categoriaData.cat7.cuarentaycuatro, f: this.categoriaData.cat7.cuarentaycuatro },
              { v: this.categoriaData.cat7.treintaycuatro, f: this.categoriaData.cat7.treintaycuatro },
