    },
    categoriaData () {
      // "Cat 1 CCT 366/06"
      // "Cat 2 CCT 366/06"
      // "Cat 3 CCT 366/06"
      // "Cat 4 CCT 366/06"
      // "Cat 5 CCT 366/06"
      // "Cat 6 CCT 366/06"
      // "Cat 7 CCT 366/06"
      return this.nodocentesCargos
        .reduce(
          (acc, cur) => {
            const subr1 = new RegExp('SubR: Cat 1', 'gm')
            const subr2 = new RegExp('SubR: Cat 2', 'gm')
            const subr3 = new RegExp('SubR: Cat 3', 'gm')
            const subr4 = new RegExp('SubR: Cat 4', 'gm')
            const subr5 = new RegExp('SubR: Cat 5', 'gm')
            const subr6 = new RegExp('SubR: Cat 6', 'gm')
            const cate1 = subr1.test(cur.subrogancia) || cur.descripcion_categoria === 'Cat 1 CCT 366/06'
            const cate2 = subr2.test(cur.subrogancia) || cur.descripcion_categoria === 'Cat 2 CCT 366/06'
            const cate3 = subr3.test(cur.subrogancia) || cur.descripcion_categoria === 'Cat 3 CCT 366/06'
            const cate4 = subr4.test(cur.subrogancia) || cur.descripcion_categoria === 'Cat 4 CCT 366/06'
            const cate5 = subr5.test(cur.subrogancia) || cur.descripcion_categoria === 'Cat 5 CCT 366/06'
            const cate6 = subr6.test(cur.subrogancia) || cur.descripcion_categoria === 'Cat 6 CCT 366/06'
            const cate7 = cur.descripcion_categoria === 'Cat 7 CCT 366/06'
            const edadNum = Number(cur.edad)
            const edad =
              edadNum <= 25
                ? 'veinticinco'
                : edadNum <= 34
                  ? 'treintaycuatro'
                  : edadNum <= 44
                    ? 'cuarentaycuatro'
                    : edadNum <= 54
                      ? 'cincuentaycuatro'
                      : edadNum <= 64
                        ? 'sesentaycuatro'
                        : 'sesentaycinco'
            return cate1
              ? { ...acc, cat1: { ...acc.cat1, [edad]: acc.cat1[edad] + 1 } }
              : cate2
                ? { ...acc, cat2: { ...acc.cat2, [edad]: acc.cat2[edad] + 1 } }
                : cate3
                  ? { ...acc, cat3: { ...acc.cat3, [edad]: acc.cat3[edad] + 1 } }
                  : cate4
                    ? { ...acc, cat4: { ...acc.cat4, [edad]: acc.cat4[edad] + 1 } }
                    : cate5
                      ? { ...acc, cat5: { ...acc.cat5, [edad]: acc.cat5[edad] + 1 } }
                      : cate6
                        ? { ...acc, cat6: { ...acc.cat6, [edad]: acc.cat6[edad] + 1 } }
                        : cate7
                          ? { ...acc, cat7: { ...acc.cat7, [edad]: acc.cat7[edad] + 1 } }
                          : acc
          },
          {
            cat1: {
              sesentaycinco: 0,
              sesentaycuatro: 0,
              cincuentaycuatro: 0,
              cuarentaycuatro: 0,
              treintaycuatro: 0,
              veinticinco: 0
            },
            cat2: {
              sesentaycinco: 0,
              sesentaycuatro: 0,
              cincuentaycuatro: 0,
              cuarentaycuatro: 0,
              treintaycuatro: 0,
              veinticinco: 0
            },
            cat3: {
              sesentaycinco: 0,
              sesentaycuatro: 0,
              cincuentaycuatro: 0,
              cuarentaycuatro: 0,
              treintaycuatro: 0,
              veinticinco: 0
            },
            cat4: {
              sesentaycinco: 0,
              sesentaycuatro: 0,
              cincuentaycuatro: 0,
              cuarentaycuatro: 0,
              treintaycuatro: 0,
              veinticinco: 0
            },
            cat5: {
              sesentaycinco: 0,
              sesentaycuatro: 0,
              cincuentaycuatro: 0,
              cuarentaycuatro: 0,
              treintaycuatro: 0,
              veinticinco: 0
            },
            cat6: {
              sesentaycinco: 0,
              sesentaycuatro: 0,
              cincuentaycuatro: 0,
              cuarentaycuatro: 0,
              treintaycuatro: 0,
              veinticinco: 0
            },
            cat7: {
              sesentaycinco: 0,
              sesentaycuatro: 0,
              cincuentaycuatro: 0,
              cuarentaycuatro: 0,
              treintaycuatro: 0,
              veinticinco: 0
            }
          }
        )
    }
  },
  methods: {
    parseCategoria (v) {
      return v === 'cat1'
        ? 'Cat 1 CCT 366/06'
        : v === 'cat2'
          ? 'Cat 2 CCT 366/06'
          : v === 'cat3'
            ? 'Cat 3 CCT 366/06'
            : v === 'cat4'
              ? 'Cat 4 CCT 366/06'
              : v === 'cat5'
                ? 'Cat 5 CCT 366/06'
                : v === 'cat6'
                  ? 'Cat 6 CCT 366/06'
                  : v === 'cat7'
                    ? 'Cat 7 CCT 366/06'
                    : v
    }
